import {GET_TICKETS, GET_TICKET, TICKETS_LOADING, CLOSE_TICKET} from './types';
import proxy from '../../config/proxy/proxy';
import axios from 'axios';

export const getTickets=()=>dispatch=>{
    dispatch(setTicketsLoading());
    let data=new FormData();
    data.append('mostrar','1');
    let config={
      "async": true,
      "crossDomain": true,
      withCredentials: true,
      "headers": {
        "Content-Type": "application/x-www-form-urlencoded",
        "Access-Control-Allow-Origin": "http://www.it3.com.ec, http://localhost:3000",
      },
      username:'it3_support',
      password:'it3_support'
    }
    axios.post(proxy+'/api/tickets/administrar_ticket.php', data, config)
      .then(function (response) {
        // handle success
        dispatch({
            type:GET_TICKETS,
            payload:response.data
        });
        
      }).catch(function (error) {
        // handle error
        console.log(error);
        //window.location.reload();
        //window.location = proxy+'/error.html';
      });
};

export const getTicket=(id_ticket)=>dispatch=>{
    dispatch(setTicketsLoading());
    let data=new FormData();
    data.append('obtener','1');
    data.append('id_ticket',id_ticket);
    let config={
      "async": true,
      "crossDomain": true,
      withCredentials: true,
      "headers": {
        "Content-Type": "application/x-www-form-urlencoded",
      },
      username:'it3_support',
      password:'it3_support'
    }
    axios.post(proxy+'/api/tickets/administrar_ticket.php', data, config)
      .then(function (response) {
        // handle success
        dispatch({
            type:GET_TICKET,
            payload:response.data
        });
        
      }).catch(function (error) {
        // handle error
        console.log(error);
        //window.location = proxy+'/error.html';
      });
};

export const closeTicket=(id)=>dispatch=>{
  dispatch(setTicketsLoading());
  let data=new FormData();
  data.append('close','1');
  data.append('id_ticket',id);
  let config={
    "async": true,
    "crossDomain": true,
    withCredentials: true,
    "headers": {
      "Content-Type": "application/x-www-form-urlencoded",
    },
    username:'it3_support',
    password:'it3_support'
  }
  axios.post(proxy+'/api/tickets/administrar_ticket.php', data, config)
      .then(function (response) {
        // handle success
        dispatch({
            type:CLOSE_TICKET,
            payload:response.data
        });
      }).catch(function (error) {
        // handle error
        console.log(error);
        //window.location = proxy+'/error.html';
      });

};

export const setTicketsLoading=()=>{
    return{
        type: TICKETS_LOADING
    };
};


